* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background-color: #eff3f7;
    height: 100%;
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
}

html, body, #root, .container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    font-size: 45px;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    border-radius: 5px;
    color: #b2b3bc;
    box-shadow: 0 13px 27px -5px rgba(50,50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50,50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 700;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2b2b2b;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    max-width: 0;
    display: flex;
    margin: 5px 0px;
}

.movie li {
    margin-right: 10px;
    font-size: 14px;
}